@mixin border-radius-prefix($radius) {
  border-radius: $radius;
  -webkit-border-radius: $radius;
  -moz-border-radius: $radius;
}

@mixin box-shadow-prefix($values) {
  box-shadow: $values;
  -webkit-box-shadow: $values;
  -moz-box-shadow: $values;
  -o-box-shadow: $values;
}

@mixin flex-box-prefix() {
  display: flex;
  display: -webkit-flex;
}

@mixin flex-flow-prefix($value) {
  flex-flow: $value;
  -webkit-flex-flow: $value;
}

@mixin justify-content-prefix($value) {
  justify-content: $value;
  -webkit-justify-content: $value;
}

@mixin align-content-prefix($value) {
  align-content: $value;
  -webkit-align-content: $value;
}

@mixin flex-prefix($values) {
  flex: $values;
  -webkit-flex: $values;
  -ms-flex: $values;
}

@mixin align-self-prefix($value) {
  align-self: $value;
  -webkit-align-self: $value;
}

@mixin placeholder-prefix {

  input::-webkit-input-placeholder,
  textarea::-webkit-input-placeholder {
    font-family: $default-font;
  }

  input::-moz-placeholder,
  textarea::-moz-placeholder {
    font-family: $default-font;
  }

  input:-ms-input-placeholder,
  textarea:-ms-input-placeholder {
    font-family: $default-font;
  }
}

@mixin animation($value) {
  animation: $value;
  -webkit-animation: $value;
  -moz-animation: $value;
}

@mixin opacity($value) {
  opacity: $value;
  -moz-opacity: $value;
}

@mixin animation-name ($animation-name) {
  @-webkit-keyframes #{$animation_name} {
    @content;
  }

  @-moz-keyframes #{$animation_name} {
    @content;
  }

  @-o-keyframes #{$animation_name} {
    @content;
  }

  @keyframes #{$animation_name} {
    @content;
  }
}